// Show type params
export type Type<T> = {
  [K in keyof T]: T[K];
} & {};

type direction = "normal" | "reverse" | "alternate" | "alternate-reverse";
type tween = "ease-in-out" | "ease-in" | "ease-out" | "linear";
type fill = "none" | "forwards" | "backwards" | "both";

export type KeyFrames = {
  name: string;
  frames: any;
  duration?: number;
  timing?: tween | string;
  count?: number | "infinite";
  direction?: direction;
  fill?: fill;
  hover?: boolean;
};

export const FadeIn: string = `{
    from {opacity: 0;}
    to {opacity: 1;}
}`;

export const ScaleUp: string = `{
    from {transform: scale(1); opacity: 0;}
    to {transform: scale(1.5); opacity: 1;}
}`;

export const Rotate: string = `{
    from {transform: rotate(0deg);}
    to {transform: rotate(360deg);}
}`;

export const SlideInLeft: string = `{
    from {transform: translateX(-100%); opacity: 0;}
    to {transform: translateX(0); opacity: 1;}
}`;

export const SlideInRight: string = `{
    from {transform: translateX(100%); opacity: 0;}
    to {transform: translateX(0); opacity: 1;}
}`;

export const SlideInUp: string = `{
    from {transform: translateY(100%); opacity: 0;}
    to {transform: translateY(0); opacity: 1;}
}`;

export const SlideInDown: string = `{
    from {transform: translateY(-100%); opacity: 0;}
    to {transform: translateY(0); opacity: 1;}
}`;

export const Flip: string = `{
  0% {margin-top:-270px;}
  5% {margin-top:-180px;}
  33% {margin-top:-180px;}
  38% {margin-top:-90px;}
  66% {margin-top:-90px;}
  71% {margin-top:0px;}
  99.99% {margin-top:0px;}
  100% {margin-top:-270px;}
}`;

export const Breath: string = `{
  from {
    animation-timing-function: ease-out;
  }
  to {
    transform: scale(1.25) translateY(-5px) perspective(1px);
    text-shadow: 0 0 20px hsl(12, 100%, 60%);
    animation-timing-function: ease-in-out;
  }
}`;

export const Pulse: string = `
  50% {
    transform: scale(1.5);
  }
}`;

export const Glitch: string = `{
  50% {
    transform: skew(180deg);
  }
}`;

export const Sheen: string = `{
  100% {
    transform: rotateZ(60deg) translate(1em, -9em);
  }
}`;

export const Glow: string = `{
  50% {
    box-shadow: 0 0 40px hsl(12, 100%, 60%);
  }
}`;

export const DropIn: string = `{
    from {
      transform:
        rotate(-30deg) translateY(-100%);
      opacity: 0;
    }
    to {
      transform:
        rotate(0deg) translateY(0%);
      opacity: 1;
    }
  }`;

export const FancyRotate: string = `{
    0% {
      transform: rotate(0turn) scale(1);
    }
    25% {
      transform: rotate(1turn) scale(1);
    }
    50% {
      transform: rotate(1turn) scale(1.5);
    }
    75% {
      transform: rotate(0turn) scale(1.5);
    }
    100% {
      transform: rotate(0turn) scale(1);
    }
  }`;

export const GrowStrink: string = `{
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.5);
    }
    100% {
      transform: scale(1);
    }
  }`;

export const Bounce: string = `{
    from {
      transform: translateY(0px);
    }
    to {
      transform: translateY(-20px);
    }
  }`;
