import { useLayoutEffect, useState } from "react";
import { KeyFrames, Type } from "../types/keyframes";

const useKeyFrames = (keyFrames: Type<KeyFrames>[]) => {
  const [internalKeys] = useState<KeyFrames[]>(keyFrames);

  const _addStylesheetRules = (animation: KeyFrames) => {
    const customStyle = document.createElement("style");
    document.head.appendChild(customStyle);
    const styleSheet = customStyle.sheet;

    styleSheet?.insertRule(
      `.${animation.name}${animation.hover ? ":hover" : ""} {
            animation-name: ${animation.name}-animation;
            animation-duration: ${animation.duration}s;
            animation-timing-function: ${
              animation.timing ? animation.timing : "ease-in-out"
            };
            animation-iteration-count: ${animation.count ? animation.count : 1};
            animation-direction: ${
              animation.direction ? animation.direction : "normal"
            };
            animation-fill-mode: ${
              animation.fill ? animation.fill : "forwards"
            };
         }
        `,
      0
    );
    styleSheet?.insertRule(
      `@keyframes ${animation.name}-animation ${animation.frames}`,
      0
    );
  };

  useLayoutEffect(() => {
    if (internalKeys.length === 0) return;
    for (const animation of internalKeys) {
      _addStylesheetRules(animation);
    }
  }, []);

  return {};
};
export default useKeyFrames;
